@import './header/header.scss';
@import './main/main.scss';
@import './rain/rain.scss';
@import './gmaps/gmaps.scss';
@import './text/text.scss';
@import './path/path.scss';

body{
    max-width: 1440px;
    background-color: #33080d;
    font-family: 'Times New Roman', Times, serif;
    margin: 0 auto;
}

.fotoContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px 15%;
    // max-width: 500px
    @media screen and (max-width: 425px){
        &{
            margin: 150px 0%;
        }
    }
    @media screen and (min-width: 425px) and (max-width: 1180px){
        &{
            margin: 150px 0%;
        }
    }
}