@import '../constants.scss';
.text{
    margin: 60px 15%;
    color: $white;
    font-size: 2em;
    &--center{
        margin-top: 50px;
        display: flex;
        justify-content: center;
    }
    &--color{
        color: #ffbc6b;
    }
    @media screen and (max-width: 425px){
        &{
            display: flex;
            justify-content: center;
            font-size: 1.5em;
            margin: 60px 10%;
            text-align: center;
        }
    }
    @media screen and (min-width: 425px) and (max-width: 768px){
        &{
            text-align: center;
            display: flex;
            justify-content: center;
            font-size: 1.5em;
            margin: 60px 10%;
        }
    }
}
