@import '../constants.scss';

.header{
    display: flex;
    height: 120px;
    align-items: center;
    justify-content: space-between;
    margin: 0 10%;
    &--font-size{
        font-size: 1.2em;
    }
    &--spaceing{
        margin: 0 10px;
    }
    &--position_left{
        margin-left: auto;
    }
    &--color_white{
        background: $white;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        // @include shadowbox();
    }
    &--color_gradient{
        background: $gradient;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        // @include shadowbox();
    }
}
