$white: #ffffff;
$gradient: linear-gradient(142deg, rgba(254,209,155,1) 0%, rgba(255,188,107,1) 100%);
$black: #2d212b;

@mixin shadowbox(){
    -webkit-box-shadow: 10px 10px 80px 6px rgba(254,209,155,1);
    -moz-box-shadow: 10px 10px 80px 6px rgba(254,209,155,1);
    box-shadow: 10px 10px 80px 6px rgba(254,209,155,1);
}

@mixin shadowbox_2(){
    -webkit-box-shadow: 10px 10px 140px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 140px -10px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 140px -10px rgba(0,0,0,0.75);
}
