@import '../constants.scss';

.path{
    display: flex;
    margin: 150px 15%;
    align-items: center;
    flex-wrap: wrap;
    //border: 1px solid $black;
    //padding: 50px;
    //border-radius: 100px;
    //background-color: #220000;
    //@include shadowbox_2();:

    &__image{
        margin-right: auto;
        border-radius: 50%;
        width: 201px;
        height: 201px;
        background-position: center;
        background-size: cover;

        &::before{
            content: "";
            //margin: 10px 15% ;
            //position: absolute;
            height: 50px;
            width: 1px !important;
            top: 100px;
            left: 50px;
            background-color: $white;
        }
    }
    &__text{
        color: $white;
        font-size: 1.5em;
        //margin-left: 100px;
        width: 600px;
    }

    @media screen and (max-width: 425px){
        &{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 150px 10%;
        }
        &__image{
            // justify-content: center;
            margin: auto;

        }
        &__text{
            color: $white;
            font-size: 1.5em;
            margin-top: 50px;
            // width: 600px;
            width: auto;
            text-align: center;
        }
    }
    @media screen and (min-width: 425px) and (max-width: 1180px){
        &{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 150px 10%;
        }
        &__image{
            // justify-content: center;
            margin: auto;

        }
        &__text{
            color: $white;
            font-size: 1.5em;
            margin-top: 50px;
            // width: 600px;
            width: auto;
            text-align: center;
        }
    }
}


